@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    margin: 0;
  }
  .btn{
    border: none;
    padding: .5rem 2rem;
    background: blueviolet;
    color: white;
    outline: none;
    font-family: inherit;
    cursor: pointer;
  }
  .btn:hover{
    background: #701fbb;
  }
  .loader{
    background-color: blueviolet;
    height: 6px;
    position: fixed;
    top: 0;
  }
  .field{
    padding: 0.5rem;
    display: flex;
  }
  .field_skeleton{
    width: 10rem;
    height: 1rem;
    border-radius: 10px;
    background-image: linear-gradient(90deg, #ddd 0px, #e8e8e8 40px, #ddd 80px);
    background-size: 600px;
    animation: shine-lines 1.6s infinite linear;
  }
  .avatar_skeleton{
    width: 10rem;
    height: 10rem;
    border-radius: 50%;
    background-image: linear-gradient(90deg, #ddd 0px, #e8e8e8 40px, #ddd 80px);
    background-size: 600px;
    animation: shine-lines 1.6s infinite linear;
  }
  .field_key{
    font-weight: bold;
    width: 7rem;
  }
  .error-modal{
    position: fixed;
    bottom: 1rem;
    right: 1rem;
    width: 15rem;
    min-height: 3rem;
    background: #ff5858;
    border-radius: 5px;
    padding: 1.7rem;
    word-break: break-word;
    color: white;
  }
  .close-btn {
    position: absolute;
    cursor: pointer;
    right: 1rem;
    top: 1rem;
    width: 1rem;
    height: 1rem;
}

.close-btn:before, .close-btn:after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    height: .7rem;
    border: 1px solid white;
}

.close-btn:before {
    transform: translate(-50%, -50%) rotate(45deg);
}

.close-btn:after {
    transform: translate(-50%, -50%) rotate(-45deg);
}

@keyframes shine-lines {
  0% {
    background-position: -100px;
  }
  40%, 100% {
    background-position: 140px;
  }
}

.user-table{
    margin: auto;
    border-spacing: 0;
}
.user-table tbody tr{
    cursor: pointer;
    border-radius: 4px;
    padding: 1.5rem;
}
.user-table td, .user-table th{
    padding: .5rem 3rem;
    text-align: left;
}
.user-table tbody tr:nth-child(odd){
    background: #f5f5f5
}
.user-table tbody tr:hover{
    outline: 1px solid blueviolet;
    background: white;
    transform: scale(1.02);
    transition: transform 0.2s ease-in-out;
}
.visited{
    background-color: #f2b5f4 !important;
    transition: background-color 0.3s ease-in-out;
}
.avatar__small{
    height: 3rem;
    border-radius: 50%;
}
.avatar__big{
    height: 10rem;
    border-radius: 50%;
}
.user-url:hover{
    color: orangered;
}
.user-wr{
    padding: 2rem;
}
.user_avatar-wr{
    padding: 2rem 0;
}
